}
.ui.module-tab.minimal .tab-content {
	border-width: 1px 0 0 0;
	padding: 1.3em 0 0;
	box-shadow: none !important;
	background: none;
}
.ui.module-tab.minimal.vertical .tab-content {
	border-width: 0 0 0 1px;
	padding: 10px 0 0 20px;
}

/* gradient */
