	margin-left: 0 !important;
}
.ui.nav li {
	display: inline-block;
	position: relative;
}

/* drop down */
.ui.nav ul {
	display: none;
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 999;
	margin-left: -1px;
}
