	display: block;
}
.ui.nav > li + li {
	border-left-width: 1px;
	border-left-style: solid;
}
.ui.nav.vertical > li + li {
	border-top-width: 1px;
	border-top-style: solid;
	border-color: inherit;
}

/* vertical navigation */
.ui.nav.vertical {
	width: 180px;
	max-width: 100%;
}
.ui.nav.vertical > li {
	float: none;
	border: none;
	width: 100%;
	clear: both;
}
