/************************************************************************************
Row BG Slider
*************************************************************************************/
.themify_builder_row .row-slider .caroufredsel_wrapper,
.themify_builder_row .sub_row-slider .caroufredsel_wrapper,
.themify_builder_row .col-slider .caroufredsel_wrapper,
.themify_builder_row .sub-col-slider .caroufredsel_wrapper {
	margin: 0 !important;
}
.themify_builder_row .row-slider-slides {
	/* Important for bg slider navigation dots to be clickable */
	z-index: 1;
	text-align: center !important;
	width: 100% !important;
	margin: 0;
	padding: 0 !important;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -15px;
	visibility: hidden;
	-webkit-transition: bottom 0.2s ease-out;
	transition: bottom 0.2s ease-out;
}
.themify_builder_row .row-slider-slides li {
	display: inline-block;
	width: 10px!important;
	cursor: pointer;
	margin: 0 3px;
}
.themify_builder_row .row-slider-dot {
	width: 10px;
	height: 10px;
	background: #fff;
	border-radius: 9em;
	display: block;
}
.themify_builder_row .row-slider-nav {
	position: absolute;
	z-index: 999;
	width: 100%;
	top: 50%;
	left: 0;
	margin-top: -.5em;
}
.themify_builder_row .row-slider-arrow {
	cursor: pointer;
	position: absolute;
	top: 50%;
	font-size: 20px;
	visibility: hidden;
	width: 18px;
	height: 18px;
	margin-top: -.5em;
	text-indent: -999em;
	text-align: center;
	border-bottom-width: 0;
}
.themify_builder_row .row-slider-prev {
	left: 0;
}
.themify_builder_row .row-slider-next {
	right: 0;
}
.themify_builder_row .row-slider-arrow:before {
	content: '';
	position: absolute;
	top: 0;
	left: 5px;
	text-indent: 0;
	width: 18px;
	height: 18px;
	line-height: 1em;
	border: 2px solid transparent;
}
.themify_builder_row .row-slider-prev:before {
	border-left-color: inherit;
	border-top-color: inherit;
	transform: rotateZ(-45deg);
}
.themify_builder_row .row-slider-next:before {
	border-right-color: inherit;
	border-top-color: inherit;
	transform: rotateZ(45deg);
	left: auto;
	right: 5px;	
}
.themify_builder_row .row-slider-dot,
.themify_builder_row .row-slider-arrow {
	color: #fff;
	opacity: 0.7;
	text-decoration: none;
	outline: none;
	-webkit-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;
}
.themify_builder_row .row-slider-dot:hover,
.themify_builder_row .row-slider-arrow:hover {
	opacity: 1;
	text-decoration: none;
}

.themify_builder_row:hover .row-slider .row-slider-prev,
.module_subrow:hover .sub_row-slider .row-slider-prev,
.module_column:hover .col-slider .row-slider-prev,
.sub_column:hover .sub-col-slider .row-slider-prev {
	left: 5px;
	visibility: visible;
}
.themify_builder_row:hover .row-slider .row-slider-next,
.module_subrow:hover .sub_row-slider .row-slider-next,
.module_column:hover .col-slider .row-slider-next,
.sub_column:hover .sub-col-slider .row-slider-next {
	right: 5px;
	visibility: visible;
}
.themify_builder_row:hover .row-slider .row-slider-slides,
.module_subrow:hover .sub_row-slider .row-slider-slides,
.module_column:hover .col-slider .row-slider-slides,
.sub_column:hover .sub-col-slider .row-slider-slides {
	bottom: 5px;
	visibility: visible;
}
/* If it's a touch device, always show direction arrows and pagination */
.builder-is-touch .themify_builder_row .row-slider-slides {
	bottom: 5px;
	visibility: visible;
}
.builder-is-touch .themify_builder_row .row-slider-prev {
	left: 5px;
	visibility: visible;
}
.builder-is-touch .themify_builder_row .row-slider-next {
	right: 5px;
	visibility: visible;
}

